var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"container"},[_vm._ssrNode("<article class=\"product\" data-v-235a1582><div class=\"left\" data-v-235a1582><h1 class=\"title\" data-v-235a1582>Punainen Gazpacho</h1><span data-v-235a1582>4 annosta</span><span class=\"recipe-title\" data-v-235a1582>Ainekset:</span><ul data-v-235a1582><li data-v-235a1582>8 Varpion tomaattia</li><li data-v-235a1582>1 Varpion kurkku</li><li data-v-235a1582>1 punainen paprika</li><li data-v-235a1582>1 keltasipuli</li><li data-v-235a1582>2 valkosipulin kynttä</li><li data-v-235a1582>4 oksaa timjamia</li><li data-v-235a1582>(rypsiöljyä freesaukseen)</li><li data-v-235a1582>1 iso viipale vaaleaa leipää</li><li data-v-235a1582>1 dl ekstra-neitsytoliiviöljy</li><li data-v-235a1582>1/2 ruukkua Varpion basilikaa</li><li data-v-235a1582>1 tl suolaa</li><li data-v-235a1582>4 rouhaisua mustapippuria myllystä</li></ul><span class=\"recipe-title\" data-v-235a1582>Valmistusohjeet</span><ol data-v-235a1582><li data-v-235a1582>Freesaa paistinpannulla pienessä määrässä rypsiöljyä pilkottu paprika, sipuli, valkosipuli ja timjaminoksat noin viisi minuuttia. Pidä lämpö matalana ja älä anna kasvisten ruskistua. Siirrä pieneen kulhoon jäähtymään. Ota timjaminoksat pois kasvisten jäähdyttyä.</li><li data-v-235a1582>Halkaise tomaatit, pilko kurkku vähän pienemmäksi, paahda leipäviipale, nypi basilikan lehdet.</li><li data-v-235a1582>Laita kaikki ainekset valmistusastiaan. Gazpacho voidaan valmistaa joko blenderissä, monitoimikoneessa tai korkeassa kulhossa sauvasekoittimella.</li><li data-v-235a1582>Aja sileäksi keitoksi ja tarjoile kylmänä. Voit keittoa valmistaessa lisätä joukkoon muutaman jääpalan, mikäli tarjoilet keiton välittömästi.</li></ol><span data-v-235a1582>* Säilyy hyvänä jääkaapissa kaksi vuorokautta.</span></div><div class=\"right\" data-v-235a1582><img"+(_vm._ssrAttr("src",require("assets/img/recipes/gazpacho.jpg")))+" data-v-23